<template>
  <div class="about container">
      <van-nav-bar fixed title="关于" left-arrow @click-left="onClickLeft"></van-nav-bar>
      <div class="about-desc">
        <img :src="logo" class="about-logo">
        <div class="about-ver">{{version}}</div>
        <p class="about-desc-txt">{{desc}}</p>
      </div>
      <div class="about-common border about-update" @click="checkVer">
        <span>版本更新</span>
        <span>已是最新版本</span>
      </div>
      <div class="about-common border" @click="toService">
        <span>客服电话</span>
      </div>
      <div class="about-common border" @click="toShare">
        <span>分享下载链接</span>
      </div>
  </div>
</template>

<script>
import { NavBar, Toast } from 'vant'

export default {
  components: {
    [NavBar.name]: NavBar
  },
  data () {
    return {
      logo: '',
      version: '1.0.0',
      desc: 'C-me是面向企业移动办公推出的一款视频会议应用,遵循"功能极致,操作极简"的产品设计理念,为用户提供召开,加入等极简的会议服务体验'
    }
  },
  methods: {
    onClickLeft () {
      this.$router.push({
        path: '/me/index',
        query: {}
      })
    },
    toService () {
      this.$router.push({
        path: '/me/about/service',
        query: {}
      })
    },
    toShare () {
      this.$router.push({
        path: '/me/about/share',
        query: {}
      })
    },
    checkVer () {
      Toast('已经是最新版本')
    }
  },
  created () {

  }
}
</script>

<style  scoped>
.about{
  font-size: 15px;
}
.about-common{
  background-color: #fff;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  padding: 0 20px;
}
.about-common::before{
  content: '';
  width: 6px;
  height: 6px;
  border: 2px solid #ccc;
  position: absolute;
  top: 50%;
  right: 15px;
  border-left-color: transparent;
  border-bottom-color: transparent;
  transform: translateY(-5px) rotate(45deg);
}

.about-logo{
  width: 50px;
  height: 50px;
  border-radius: 3px;
}
.about-desc{
  text-align: center;
  padding: 40px 0 25px 0;
}
.about-ver{
  padding: 10px 0 50px 0;
}
.about-desc-txt{
  font-size: 12px;
  padding: 0 20px;
  line-height: 1.7em;
}
.about-update{
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
}
</style>



